.App {
  text-align: center;
  font-family: 'Fira Sans Condensed';
  color: #030027;
  height: 100vh;
}

.landingPage {
  background:#CCDBDC;
}

.landingPage h1 {
  font-family: 'Indie Flower';
  margin: 0 auto;
  padding-top: 100px;
  font-size: 6rem;
}

.landingPage h2 {
  margin: 0px;
}

.landingPage h3 {
  margin-bottom: 80px;
}

.landingPage button {
  background: #36213E;
  color: #FDFFFC;
  border: none;
  padding: 20px 50px;
  font-size: 2rem;
  margin-bottom: 125px;
}

.formSection form {
  display: flex;
  flex-direction: column;
  width: 60%;
  margin: 0 auto;
}

.formSection input {
  text-align: left;
  width: 70%;
}

.formSection label {
  font-size: 1.5rem;
  text-align: left;
  width: 30%;
}

.formSection .input {
  padding: 15px;
  width: 100%;
  margin: 0 auto;
  text-align: left;
}

.input input {
  padding: 10px;
  margin: 0 10px;
}

.formSection h1 {
  padding-top: 30px;
  font-size: 4.3rem;
  color: #030027;
}

.appHeader {
  background: #36213E;
}

.navMenu {
  display: flex;
  direction: row;
  justify-content: space-between;
  color: #FDFFFC;
}

.navMenu ul {
  display: flex;
  flex-direction: row;
  list-style: none;
  margin: 0px;
}


.navMenu li {
  padding: 20px;
  font-size: 1.5rem;
}

.navMenu p {
  font-size: 1.5rem;
  padding-left: 20px;
}


.submitButton button {
  font-size: 1.5rem;
  padding: 20px;
}

.myEntries {
  background: #FDFFFC;
  color: #030027;
}

.myEntries h1 {
  font-family: 'Indie Flower';
  font-size: 4rem;
  margin: 0;
  padding-top: 30px;
}

.myEntries ul {
  list-style: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.myEntries li {
  background: #c5c5c5;
  margin: 20px;
  width: calc(33% - 40px);
  color: #FDFFFC;
}

.myEntries button {
  width: 100%;
  background: #c5c5c5;
}

.appFooter {
  background: #030027;
  color: #FDFFFC;
  height: 5vh;
}

.appFooter p {
  margin: 0 auto;
  padding-top: 10px;
  text-align: center;
}
